import React, { PureComponent, Fragment } from 'react';
import {
  Dialog,
  Typography,
  IconButton,
  withWidth,
  Divider,
  withMobileDialog,
  DialogActions,
  Button,
  DialogContent,
  DialogTitle,
  Tooltip,
} from '@material-ui/core';
import Info from '@material-ui/icons/Info';
import { compose } from 'recompose';

class AdditionalInfoDialog extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
    };
  }

  handleClick = () => {
    this.setState(state => ({
      open: !state.open,
    }));
  };

  handleClose = () => {
    this.setState({
      open: false,
    });
  };

  render() {
    const {
      maxWidth,
      titleEN,
      titleTH,
      children,
      format,
      width,
    } = this.props;
    return (
      <Fragment>
        <Tooltip
          title={width === 'xs' ? '' : 'ข้อมูลเพิ่มเติม'}
          disableTouchListener
          disableFocusListener
        >
          <IconButton onClick={this.handleClick}>
            <Info fontSize="small" />
          </IconButton>
        </Tooltip>
        <Dialog
          fullScreen={this.props.fullScreen}
          scroll="paper"
          maxWidth={maxWidth}
          open={this.state.open}
          onClose={this.handleClose}
        >
          <DialogTitle>
            <Fragment>
              {titleTH}

              <Typography
                component="span"
                color="textSecondary"
                variant="subtitle2"
              >
                {titleEN}
              </Typography>
            </Fragment>
            <Divider style={{ marginTop: 15 }} />
          </DialogTitle>
          <DialogContent>
            <Typography
              color="textSecondary"
              gutterBottom
              variant="subtitle1"
            >
              คำอธิบาย
            </Typography>
            <Fragment>{children}</Fragment>
            <br />
            <Typography
              gutterBottom
              color="textSecondary"
              variant="subtitle1"
            >
              รูปแบบการกรอก
            </Typography>
            <Typography color="textPrimary">{format}</Typography>

            <DialogActions>
              <Button
                variant="outlined"
                size="medium"
                color="primary"
                onClick={this.handleClose}
              >
                ปิด
              </Button>
            </DialogActions>
          </DialogContent>
        </Dialog>
      </Fragment>
    );
  }
}

export default compose(
  withMobileDialog({ breakpoint: 'xs' }),
  withWidth()
)(AdditionalInfoDialog);
