import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../shared/handleChange';
import { InputAdornment, withWidth, Tooltip } from '@material-ui/core';
import AdditionalInfoDialog from './AdditionalInfoDialog';
import { compose } from 'recompose';

class RegularTextField extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  componentDidMount() {
    setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  render() {
    const {
      name,
      label,
      helperText,
      placeholder,
      validators,
      errorMessages,
      required,
      type,
      dialogTitleTH,
      dialogFormat,
      dialogContent,
      dialogWidth,
      width,
    } = this.props;
    return (
      <Tooltip
        disableTouchListener
        disableFocusListener
        title=""
        enterDelay={650}
        placement="left"
      >
        <TextValidator
          name={name}
          id={name}
          required={required}
          onBlur={this.handleBlur}
          type={type}
          onChange={this.handleChange}
          value={this.state.value}
          label={label}
          style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
          helperText={helperText}
          margin={width === 'xs' ? 'normal' : 'dense'}
          placeholder={placeholder}
          variant="outlined"
          fullWidth
          validators={validators}
          errorMessages={errorMessages}
          InputProps={{
            endAdornment: dialogContent && (
              <InputAdornment position="end">
                <AdditionalInfoDialog
                  maxWidth={dialogWidth ? dialogWidth : 'xs'}
                  titleTH={dialogTitleTH ? dialogTitleTH : helperText}
                  titleEN={label}
                  format={dialogFormat}
                >
                  {dialogContent}
                </AdditionalInfoDialog>
              </InputAdornment>
            ),
          }}
        />
      </Tooltip>
    );
  }
}

export default compose(withWidth())(RegularTextField);
